.App {
  text-align: left;
}

div {
  box-sizing: border-box;
}

h2 {
  font-size: 40px;
}

h4 {
  font-size: 20px;
  margin: 10px 0 20px 0;
}

p {
  margin: 0;
}

.d-flex {
  display: flex;
}

.nav {
  display: flex;
  align-items: center;
  padding-left: 20px;
  width: 100%;
  max-height: 100px;
  height: 100%;
  background-color: #000;
  color: #fff;
}

.list {
  padding: 10px 20px;
  border-bottom: 1px solid #ddd;
  font-weight: bold;
}

.list .d-flex span {
  margin-right: 5px;
  
}

.list .d-flex p {
  margin-left: 20px;
  font-weight: normal;
}